"use client";
import { useState } from "react";
import Image from "next/image";
import styles from "./EventsSection.module.css";

export default function EventsSection() {
  // ========================
  // DYNAMIC DATA SOURCE
  // ========================
  const allEvents = [
    {
      id: 1,
      title: "Techtonic Summit: Ideas That Shake The Future",
      date: "October 18, 2024",
      category: "Technology",
      month: "October",
      school: "Engineering",
      img: "/images/techtonic.jpg",
      upcoming: true,
    },
    {
      id: 2,
      title: "Summer Beats Festival 2025",
      date: "October 16, 2025",
      category: "Festival",
      month: "October",
      school: "Design",
      img: "/images/summerbeats.jpg",
      description:
        "Writing for The Conversation, Professor Hayley Fowler, Paul Davies, and Simon Lee discuss how the rapidly warming climate impacts creativity.",
    },
    {
      id: 3,
      title: "Business Today Codestorm 2.0",
      date: "October 10, 2024",
      category: "Business",
      month: "October",
      school: "Management",
      img: null,
      bgColor: "#002E6E",
    },
    {
      id: 4,
      title: "Annual Fest that celebrates everything JSS stands for",
      date: "August 18, 2024",
      category: "Cultural",
      month: "August",
      school: "Engineering",
      img: null,
      bgColor: "#B72833",
    },
    {
      id: 5,
      title: "Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
      date: "July 20, 2024",
      category: "Education",
      month: "July",
      school: "Architecture",
      img: "/images/event5.jpg",
    },
    {
      id: 6,
      title: "Sed ut perspiciatis unde omnis iste natus error sit.",
      date: "August 14, 2024",
      category: "Workshop",
      month: "August",
      school: "Robotics",
      img: "/images/event6.jpg",
    },
  ];

  // ========================
  // FILTER STATE
  // ========================
  const [filters, setFilters] = useState({
    category: "All",
    month: "All",
    school: "All",
  });

  const months = ["All", "January", "July", "August", "October"];
  const schools = [
    "All",
    "Engineering",
    "Design",
    "Management",
    "Architecture",
    "Robotics",
  ];

  // ========================
  // FILTER LOGIC
  // ========================
  const filteredEvents = allEvents.filter((event) => {
    const byMonth =
      filters.month === "All" || event.month === filters.month;
    const bySchool =
      filters.school === "All" || event.school === filters.school;
    return byMonth && bySchool;
  });

  const upcomingEvent = allEvents.find((e) => e.upcoming);

  const handleFilter = (key, value) => {
    setFilters((prev) => ({ ...prev, [key]: value }));
  };

  // ========================
  // RENDER
  // ========================
  return (
    <section className={styles.eventsSection}>
      {/* Main Banner */}
      <div className={styles.bannerWrapper}>
        {upcomingEvent && (
          <>
            <Image
              src={upcomingEvent.img}
              alt={upcomingEvent.title}
              layout="responsive"
              width={1200}
              height={400}
              className={styles.bannerImage}
            />
            <div className={styles.bannerTextBox}>
              <p className={styles.upcomingTag}>UPCOMING EVENTS</p>
              <h3 className={styles.bannerTitle}>
                {upcomingEvent.title.toUpperCase()}
              </h3>
              <p className={styles.bannerDate}>{upcomingEvent.date}</p>
            </div>
          </>
        )}
      </div>

      {/* Filter Buttons */}
      <div className={`d-flex justify-content-end gap-2 ${styles.filters}`}>
        <select
          className="form-select"
          onChange={(e) => handleFilter("month", e.target.value)}
          value={filters.month}
        >
          {months.map((m) => (
            <option key={m}>{m}</option>
          ))}
        </select>
        <select
          className="form-select"
          onChange={(e) => handleFilter("school", e.target.value)}
          value={filters.school}
        >
          {schools.map((s) => (
            <option key={s}>{s}</option>
          ))}
        </select>
      </div>

      {/* Secondary Banner with Text */}
      <div className={`row align-items-center mt-5 ${styles.secondarySection}`}>
        <div className="col-md-7">
          <div className={styles.secondaryImageWrapper}>
            <Image
              src="/images/summerbeats-banner.jpg"
              alt="Secondary Event"
              layout="responsive"
              width={700}
              height={400}
              className={styles.secondaryImage}
            />
          </div>
        </div>
        <div className="col-md-5">
          <div className={styles.secondaryText}>
            <p className={styles.eventDate}>October 16, 2025</p>
            <h3 className={styles.eventTitle}>SUMMER BEATS FESTIVAL 2025</h3>
            <p className={styles.eventDesc}>
              Writing for The Conversation, Professor Hayley Fowler and Simon
              Lee discuss how the rapidly warming climate influences creativity.
            </p>
          </div>
        </div>
      </div>

      {/* Event Cards */}
      <div className={`row mt-5 ${styles.cardsRow}`}>
        {filteredEvents.map((event) => (
          <div key={event.id} className="col-md-4 mb-4">
            <div
              className={`${styles.eventCard} ${
                !event.img ? styles.textOnlyCard : ""
              }`}
              style={
                !event.img ? { backgroundColor: event.bgColor || "#EEE" } : {}
              }
            >
              {event.img ? (
                <Image
                  src={event.img}
                  alt={event.title}
                  width={400}
                  height={250}
                  layout="responsive"
                  className={styles.eventImage}
                />
              ) : null}
              <div className={styles.cardBody}>
                <p className={styles.cardDate}>{event.date}</p>
                <h5 className={styles.cardTitle}>{event.title}</h5>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
